        Content: "褥疮主要是一位老年患者，孙医生讲他的腰邸处有十厘米直径的圆形溃疡口。大医院都不敢接诊，他来到孙医生这里，由于伤口的原因，所到之处散发着浓浓的臭味，老人虚弱地说：“每天忍受着地狱般疼痛的折磨。”孙医生只是给了他一瓶药，名叫消肿通经活络液，具有祛腐生肌的作用，要他每天坚持喷。没想到一个月后，老人的伤口完全愈合，这真是奇迹，这是西医的抗生素、植皮根本做不到的。另外一位女患者是糖尿病足，因为摔伤后血糖高血流不畅，伤口一直不愈合，甚至溃烂。孙医生还是给了他几瓶很便宜的消肿活络液，病人很快就恢复了。",
